@import url(https://fonts.googleapis.com/css2?family=Nunito+Sans:ital,wght@0,300;0,600;0,800;1,300;1,600;1,800&display=swap);
::root{
  --light-mode-text:hsl(200, 15%, 8%);
  --light-mode-background:hsl(0, 0%, 98%);
  --white:hsl(0, 0%, 100%)
}

*,
*::after,
*::before {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}



a {
  all:unset;
  color:var(--light-mode-text);
  cursor:pointer;
}

body {
  width: 100%;
  font-family: 'Nunito Sans', sans-serif;
  font-size: 14px;
  font-weight: 600;
}





/* utils */

.container {
    width: 90%;
}

@media screen and  (max-width:480px) {
    .container {
        width: 95%;
    }
}
header {
    border-bottom: 3px solid hsl(0, 0%, 0%,0.05);
    background-color: #fff;
}
.app__header {
    height: 3rem;
    margin:0 auto;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}


.app__header button {
    all: unset;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    cursor: pointer;
}
.app__header button i {
    margin-right:0.8em;
}

@media screen and (max-width:480px) {
    .app__header h1 {
        font-size: 1.2em;
    }
}
main {
    background-color: hsl(0, 0%, 98%);
}
.controls {
    margin: 0 auto;
    /* padding: 2rem 0; */
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
}


#search , #filter {
    margin-top:2rem;
}

#search {
    position: relative;
}

#search i {
    position: absolute;
    top: 10px;
    left: 15px;
    z-index: 1;
}

#search input {
    display: inline-block;
    width: 300px ;
    outline: none;
    border: none;
    border-radius: 3px;
    padding: 10px 10px 10px 40px ;
    box-shadow:
    0px 0px 3px 2px rgba(0, 0, 0, 0.062),
    0px 0px 3px 2px rgba(0, 0, 0, 0.062);
    position: relative;
}

#filter{
    position: relative;
    cursor: pointer;
}

#filter i {
    position: absolute;
    right: 10px;
    top: 10px;
    z-index: 0;
}

#filter select {
    display: inline-block;
    /* background-color: transparent ; */
    cursor: pointer;
    width: 160px ;
    padding: 10px;
    outline: none;
    border: none;
    border-radius: 3px;
    box-shadow:
    0px 0px 3px 2px rgba(0, 0, 0, 0.062),
    0px 0px 3px 2px rgba(0, 0, 0, 0.062);
    -webkit-appearance: none;
    -moz-appearance: none;
    text-indent: 1px;
    text-overflow: '';
}
.countries {
    margin:2rem auto;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
}
.coutnry-card {
    display: block;
    width: 180px;
    min-height:280px ;
    margin: 20px;
    border-radius: 5px;
    background-color: #fff;
    
}
.coutnry-card .details {
    padding: 10px;
}
.coutnry-card img {
    border-top-left-radius: 4px;
    border-top-right-radius: 4px;
    width: 100%;
    object-fit: contain;
    
}

.coutnry-card h2 {
    font-weight: 700;
    font-size: 1.2rem;
    margin: 0.8rem 0;
}



.country-page {
    margin:0 auto;
}

.back {
    height: 6rem;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
}
.back button {
    all:unset;
    cursor: pointer;
    box-shadow:
    0px 0px 3px 2px rgba(0, 0, 0, 0.062),
    0px 0px 3px 2px rgba(0, 0, 0, 0.062);
    padding: 0.4rem 1.6rem;
    border-radius: 3px;
}
.back button i {
    margin-right: 10px;
}

.country-info {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
}

.country-info  h2{
    margin: 0.6em 0;;
}

.country-info dt {
    font-weight: 700;
}
.country-info ul {
    list-style: none;
}

.country-info img {
    width: 300px;
    max-height: 200px;
    /* margin-right: 6rem; */
}

.data {

}

.data-block {
    width: 300px;
    /* width: auto; */
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
}
.data-block article {
    margin-right: auto;
}
.borders {
    max-width: 300px;
    min-height: 120px;
}
.borders ul{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
    list-style: none;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    margin-top: 1em;
    margin-bottom: 2em;
    margin-left:5px;
    margin-right:5px;
}
.borders li {
    margin-top: 10px;
}
.borders button {
    all:unset;
    cursor: pointer;
    box-shadow:
    0px 0px 3px 2px rgba(0, 0, 0, 0.062),
    0px 0px 3px 2px rgba(0, 0, 0, 0.062);
    padding: 0.3rem 0.9rem;
    border-radius: 3px;
}
.borders dl {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-flex-direction: column;
            flex-direction: column;
}
.borders dt {
    display: inline;
}
